import style from "./BrandAgeConBottomSheet.module.css"
import AgeSign from "../../asset/icons/18PlusSign.png"
import ThanksLogo from "../../asset/icons/thanksLogo.png"
import { useNavigate } from "react-router-dom";
import  Api from "../../utils/Api/Api";

export const BrandAgeConBottomSheet = ({toggleModal, modalData})=>{

    const navigate = useNavigate()


    const RedirectHandling = async (button, text) =>{

        if(text === 'View my subscription' || text === "NO I'M NOT") {
            navigate(button.action);
        }
        else if(text === 'OKAY,Got it') {
            toggleModal()
        }
        else if(text === 'YES, I AM ABOVE 18') {

            const payload = {
                vendor:button.action
            }
            await Api.AgeRestrictionUpdate(payload);
            toggleModal();
        }
    }
    return(
        <div onClick={() => {
            if(modalData.type != 1) {
                toggleModal()
            }
            }} className={style.overlay}>
           <div onClick={(e)=>{ e.stopPropagation() }} className={style.popUp}>
                <img onClick={() => {
                    if(modalData.type != 1) {
                        toggleModal()
                    }
                    }} src={AgeSign} alt="18-plus-sign" className={style.ageSign}></img>
                <div className={style.headText}>
                    {modalData.title}
                </div>
                <div className={style.disclamerText}>
                    {modalData.text}
                </div>
                <div className={style.separator}>
                    <div className={style.bar}></div>
                    <img className={style.thanksImg} src={ThanksLogo} alt="Thanks-Img"/>
                    <div className={style.bar}></div>
                </div>
                <div className={style.buttonCollection}>
                    <button onClick={() => RedirectHandling(modalData.button2, modalData.button2.text)} className={style.cutButton}>{modalData.button2.text}</button>
                    <button onClick={() => RedirectHandling(modalData.button1, modalData.button1.text)} className={style.acceptButton}>{modalData.button1.text}</button>
                </div>
                <div className={style.bar2}>
                </div>
           </div>
        </div>
    )
}