import axios from 'axios';
import { useNavigate } from "react-router-dom";

import { ErrorBoundary } from 'react-error-boundary'
import { ErrorFallback } from './components/Error/Error';

import './App.css';
import { WebSiteRoutes } from './WebSiteRoutes/WebSiteRoutes';
// import {PaymentDetailsCard, UserPersonalDetailsCard,DiscountCouponDetailsCard ,ApplyDiscountCouponScreen ,ApplyDiscountCouponScreenCouponCard } from './components';


function App() {
const navigate = useNavigate()


axios.defaults.baseURL = process.env.REACT_APP_API_URL;
axios.defaults.headers.common.Authorization = localStorage.getItem('accesstoken');

axios.interceptors.response.use((response) => response, (error) => {
  if (error && error.response && error.response.status === 401) {
    
    localStorage.removeItem('accesstoken');
    window.location.href = '/login' 
  }
  return error;
});
  return (
    <div className="App">
      <ErrorBoundary FallbackComponent={ErrorFallback}>
        <WebSiteRoutes navigate={navigate} />
      </ErrorBoundary>  
    </div>
  );
}

export default App;
