import React, { useState, useEffect, useRef } from "react";
import { NavBar } from "../../commonComponents";
import { ErrorFallback } from "../../components/Error/Error";
import { Loading } from "../../components/Loading/Loading";
import styles from "./Marketplace.module.css"
import FleekIcon from "../../asset/icons/FleekIcon.png"
import { MarketPlaceCard, CoinNWallet } from "../../components"
import Api from "../../utils/Api/Api";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import cx from 'classnames';
import InfiniteScroll from 'react-infinite-scroll-component';

function Arrow() {
    return (
        <div className={styles.hide}>
        </div>
    );
}


export const Marketplace = () => {

    const [loading, setLoading] = useState(true);
    const [hasError, setError] = useState(false);
    const [brandImage, setBrandImage] = useState([]);
    const [brandData, setBrandData] = useState([]);
    const [slideNumber, setSlideNumbar] = useState(0);
    const [hasMore, setHasMore] = useState(true);
    const [page, setPage] = useState(1);

    let settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        initialSlide: slideNumber,
        prevArrow: <Arrow />,
        nextArrow: <Arrow />,
        afterChange(currentSlide) {
            setSlideNumbar(currentSlide)
        }
    };

    const fetchMarketPlaceData = async () => {
        try {
            const promiseArr = [ Api.MarketPlaceData({ page })]
            if(page === 1 ) {
                promiseArr.push(Api.MarketPlaceBanner())
            }
            const [{ data }, banner ] = await Promise.all(promiseArr);

            if (banner && banner.data && banner.data.Marketplace && banner.data.Marketplace.length) {
                const bannerImages = banner.data.Marketplace.map((b) => {
                    return b.banner_url;
                })
                setBrandImage(bannerImages)
            }
            if (data && data.results && data.results.length) {
                setBrandData([...brandData,...data.results]);
                if(!data.next) {
                    setHasMore(false)
                }
                setPage(page + 1);

            }
        }
        catch (err) {
            console.log('=======err==========', err)
            setError(true);
        }
        setLoading(false)
    }
    const sliderRef = useRef(null);
    sliderRef.current?.slickGoTo(slideNumber);
    useEffect(() => {
        fetchMarketPlaceData();
    }, [])

    useEffect(() => {
        const callEveryInterval = setTimeout(() => {
            setSlideNumbar((pre_slide) => ((pre_slide + 1) % brandImage.length))
        }, 2000);
        return (() => {
            clearTimeout(callEveryInterval)
        })
    })

    if (hasError) {
        return <ErrorFallback />
    }
    if (loading) {
        return <Loading />
    }


    return (
        <InfiniteScroll
        dataLength={brandData.length} //This is important field to render the next data
        next={fetchMarketPlaceData}
        hasMore={hasMore}
        loader={<h4>Loading...</h4>}
        endMessage={
            <p style={{ textAlign: 'center' }}>
                <b>Yay! You have seen it all</b>
            </p>
        }
        // below props only if you need pull down functionality
        refreshFunction={() => brandData}
        pullDownToRefresh
        pullDownToRefreshThreshold={50}
        pullDownToRefreshContent={
            <h3 style={{ textAlign: 'center' }}>&#8595; Pull down to refresh</h3>
        }
        releaseToRefreshContent={
            <h3 style={{ textAlign: 'center' }}>&#8593; Release to refresh</h3>
        }
    >
        <div className={styles.marketplace}>
            <NavBar />
            <div className={styles.headcontent}>
                <div className={styles.walletContainer}>
                    <CoinNWallet />
                </div>

                <div className={styles.sliderWrapper}>
                    <Slider ref={sliderRef} {...settings}>
                        {
                            brandImage.map((ele, idx) => (
                                <img key={idx} className={styles.bannerImage} alt="bannerImage" src={ele || "https://images.theconversation.com/files/57582/original/x9bsgdm2-1409185219.jpg?ixlib=rb-1.1.0&q=45&auto=format&w=1200&h=1200.0&fit=crop"} />
                            ))
                        }
                    </Slider>
                </div>
                {/* <img className={styles.bannerImage} alt="bannerImage" src= {brandImage[0]|| "https://images.theconversation.com/files/57582/original/x9bsgdm2-1409185219.jpg?ixlib=rb-1.1.0&q=45&auto=format&w=1200&h=1200.0&fit=crop"}  /> */}
                <div className={styles.dotContainer}>
                    {
                        brandImage.map((_, idx) => (
                            <div key={idx} onClick={() => {
                                setSlideNumbar(idx)
                            }} className={idx === slideNumber ? cx(styles.dot, styles.active) : styles.dot}>

                            </div>
                        ))
                    }
                </div>
            </div>
            <div className={styles.restContent}>
                <p className={styles.sectionTitle}>Explore all brands</p>
                <div className={styles.cardContainer}>
                    {
                        brandData.map((d) => {
                            return <MarketPlaceCard data={d} key={d.vendor_id} />
                        })
                    }

                </div>
            </div>
        </div>
    </InfiniteScroll>
        

    )
}
