import styles from "./ProfileMobComp.module.css"
import React , {useState} from "react";
import chevronLeft from "../../asset/icons/chevronLeft.png";
import CameraIcon from "../../asset/icons/CameraIcon.png"
import cx from 'classnames';


export const ProfileMobComp = ({chnageScrren})=>{
    const [showBottomSheet,setShowBottomSheet] = useState(false);
    const toggleBottomSheet = ()=>{
        setShowBottomSheet((pre_state)=>(!pre_state))
    }
    const hasImage = false;
    return(
        <div>
            <div className={styles.header}>
                <p className={styles.headText}>Profile</p>
                <img onClick={()=>chnageScrren("home")} className={styles.chevronLeft} src={chevronLeft} alt="chevronLeft"/>
            </div>
            <div className={styles.otherDetail}>
                <div onClick={toggleBottomSheet} className={styles.userImgContainer}>
                    {
                        hasImage ? 
                            <img className={styles.userMainImg}/>
                            :
                            <p className={styles.userImgLetter}>A</p>
                    }
                    <img className={styles.CameraIcon} src={CameraIcon} alt="CameraIcon"/>
                </div>
                <div className={cx(styles.inputBundle,styles.firstBundle)}>
                    <p className={styles.inputTitle}>Name</p>
                    <div className={styles.inputWrapper}>
                        <input placeholder="Enter Your Name" type="text" className={styles.input}/>
                        {/* <img onClick={clearInput} className={number.length>0 ?cx(styles.crossSign,styles.display): styles.crossSign} src={crossIcon} alt="crossIcon" /> */}
                    </div>
                </div>
                <div className={styles.inputBundle}>
                    <p className={styles.inputTitle}>Phone Number</p>
                    <div className={cx(styles.inputWrapper,styles.disable)}>
                        <p className={styles.countryCode}>+91</p>
                        <div className={cx(styles.input,styles.disable)}>
                            {"9876543210"}
                         </div>   
                        {/* <img onClick={clearInput} className={number.length>0 ?cx(styles.crossSign,styles.display): styles.crossSign} src={crossIcon} alt="crossIcon" /> */}
                    </div>
                </div>
                <div className={styles.inputBundle}>
                    <p className={styles.inputTitle}>E-mail</p>
                    <div className={styles.inputWrapper}>
                    <input placeholder="Enter Your Email" type="email" className={styles.input}/>
                        {/* <img onClick={clearInput} className={number.length>0 ?cx(styles.crossSign,styles.display): styles.crossSign} src={crossIcon} alt="crossIcon" /> */}
                    </div>
                </div>
                <div className={styles.inputBundle}>
                    <p className={styles.inputTitle}>Date of birth</p>
                    <div className={styles.inputWrapper}>
                    <input placeholder="Enter Your DateOfBirth" type="date" className={styles.input}/>
                        {/* <img onClick={clearInput} className={number.length>0 ?cx(styles.crossSign,styles.display): styles.crossSign} src={crossIcon} alt="crossIcon" /> */}
                    </div>
                </div>
                <div className={styles.inputBundle}>
                    <p className={styles.inputTitle}>Gender</p>
                    <div className={styles.chipsContainer}>
                        <div className={cx(styles.chip,styles.active)}>
                            Male
                        </div>
                        <div className={styles.chip}>
                            Female
                        </div>
                        <div className={styles.chip}>
                            Others
                        </div>
                    </div>
                </div>
                <div className={styles.spacer}>

                </div>
                <div className={styles.mobileButtonContainer}>
                    <button  className={styles.payButton}>
                        SAVE CHANGES
                    </button>
                </div>
            </div>
            {
                showBottomSheet && 
                <div onClick={()=>{ toggleBottomSheet()}} className={styles.overlay}>
                    <div onClick={(e)=>{ e.stopPropagation() }} className={styles.popUp}>
                        <p className={styles.overlaytext1}>Choose a picture</p>
                        <div className={styles.overlayBar}>
                        </div>
                        <p className={styles.overlaytext2}>SELECT FROM GALLERY</p>
                        <p className={styles.overlaytext2}>TAKE A PICTURE</p>
                        <p className={cx(styles.overlaytext2,styles.red)}>REMOVE PROFILE PICTURE</p>
                    </div>
                </div>
            }
        </div>
    )
}