import styles from "./OrderDetail.module.css"
import chevronLeft from "../../asset/icons/chevronLeft.png";
import FleekIcon from "../../asset/icons/FleekIcon.png"
import forward from "../../asset/icons/whiteForwardCircle.png"
import { ErrorFallback } from "../../components/Error/Error";
import { Loading } from "../../components/Loading/Loading";
import  Api from "../../utils/Api/Api";
import React , {useState, useEffect} from "react"
import { NavBar } from "../../commonComponents";
import { useParams } from "react-router-dom";

import { ProductPurchaseCard ,SubscriptionCouponDetailsCard , YoutubeLinkCard ,
    PaymentDetailsCard ,PaymentInstrumentDetailsCard} from "../../components";

export const OrderDetail = ()=>{
    const { order_id } = useParams();

    const [loading,setLoading] = useState(true);
    const [hasError,setError] = useState(false);
    const [orderDetail,setOrderDetail] = useState([]);

    useEffect(()=>{
        const fetchCartData = async ()=>{
            try {
                let { data } = await Api.GetOrderDetails(order_id);
                setOrderDetail(data);

            }
            catch(err) {
                setError(true);
            }
            setLoading(false)
        }
        fetchCartData();
    },[])

    if(hasError) {
        return <ErrorFallback />
    }
    if(loading) {
        return <Loading />
    }

    const { payment, youtube_url, subscription_plan: {code_redemption_url} = {}, cost } = orderDetail;
    const paymentDetailsCard = {mrp_price:cost, effective_cost: payment.amount, cost: payment.amount} 
    return(
        <>
            <div className={styles.headcontent}>
                <NavBar/>
            </div>
            <div className={styles.content}>
                <div className={styles.header}>
                    <img className={styles.chevronLeft} src={chevronLeft} alt="chevronLeft"/>
                    <p className={styles.headText}>Order Confirmed</p>
                    <p className={styles.helpText}>HELP</p>
                </div>
                <div className={styles.detailContainer}>
                    <div className={styles.cardContainer}>
                        <ProductPurchaseCard cartData={orderDetail} orderPage={true} />
                        <SubscriptionCouponDetailsCard orderDetail={orderDetail}/>
                        {youtube_url && <YoutubeLinkCard youtube_url={youtube_url}/>}
                        <div className={styles.mobileButtonContainer}>
                            <button onClick={() => window.open(code_redemption_url, '_blank')} className={styles.nextButton}>
                                ACTIVATE NOW
                                <img className={styles.forward} alt="forward" src={forward}/>
                            </button>
                        </div>
                    </div> 
                    <div className={styles.otherDetail}>
                        <div className={styles.orderDetail}>
                            <p className={styles.orderId}>Order ID: {payment.order_representation_id}</p>
                            <p className={styles.orderPlaceText}>Order Placed</p>
                            <p className={styles.date}>on 9 May, 2020</p>
                        </div>
                        <PaymentDetailsCard cartData={paymentDetailsCard}/>
                        <PaymentInstrumentDetailsCard payment = {payment} />
                    </div>
                </div>       
            </div>     
        </>
    )
}