import styles from "./NumberEntryPopUp.module.css";
import React,{ useState ,useRef } from "react";
import cx from 'classnames';
import { LoginSignupCommon } from "../../commonComponents";
import crossIcon from "../../asset/icons/crossCircle.png"

export const NumberEntryPopUp = ({toggle, GetOtp})=>{
    const [number,setNumber] = useState("");
    const input = useRef();
    const handleChange = (e)=>{
        if(e.target.value.length <=10 )
        setNumber(e.target.value)
    }
    const stopPropagation = (e)=>{
        e.stopPropagation();
    }

    const clearInput = ()=>{
        setNumber("");
        input.current.focus();
    };

    const handleToggle = ()=>{
        const { innerWidth: width } = window;
        if(width>=1280){
            return;
        }
        toggle();
    }
    return(
        <div onClick={handleToggle} className={styles.overlay}>
            <div onClick={stopPropagation} className={styles.popUp}>
                <div className={styles.detail}>
                    <p className={styles.title1}>Enter your number</p>
                    <p className={styles.title2}>We will send OTP to this number</p>
                    <p className={styles.phoneTitle}>Phone number</p>
                </div>
                <LoginSignupCommon title={"Welcome to Fleek"} subtitile={"Enter your phone number to continue"} />
                <label className={styles.title}>Phone number</label>
                
                <div className={styles.inputWrapper}>
                    <p className={styles.countryCode}>+91</p>
                    <input 
                        onWheelCapture={e => {
                            e.target.blur()
                        }}
                         ref={input} onChange={handleChange} value={number} type="number" className={styles.input}/>
                    <img onClick={clearInput} className={number.length>0 ?cx(styles.crossSign,styles.display): styles.crossSign} src={crossIcon} alt="crossIcon" />
                </div>
                <button 
                    className={number.length === 10 ? cx(styles.continueButton,styles.active) :styles.continueButton}
                    onClick = {() =>  GetOtp(number) }
                    >
                    CONTINUE
                </button>
            </div>
        </div>
    )
}