import { SingleBrandPage ,ProductPage,Cart,VerifyNumber ,SignUp ,Marketplace, Login,Checkout ,
        Order,MyOrder , OrderDetail ,Account} from "../Pages";

import { Routes, Route ,Outlet, Navigate} from "react-router-dom";

export const WebSiteRoutes = (props) => {
  const access_token = localStorage.getItem('accesstoken')
    return (
      <Routes>
        <Route path="brand" element={<Outlet/>} >
            <Route path=":vendor_id" element={<SingleBrandPage navigate={props.navigate}/>} />
        </Route>
        <Route path="product" element={<Outlet/>} >
            <Route path=":product_id" element={<ProductPage/>} />
        </Route>
        <Route path="cart" element={<Cart/>}>
        </Route>
        <Route path="checkout" element={<Checkout/>}>
        </Route>
        {
          !access_token &&
          <Route path="login" element={<Outlet/>} >
          <Route path="verify-user" element={<VerifyNumber navigate={props.navigate}/>} />
          <Route path="" element={<Login navigate={props.navigate}/>}/>
         </Route> 
        }
        <Route path="order" element = {<Order/>}/>
        <Route path="signup" element={<SignUp navigate={props.navigate}/>} />
        <Route path="/" element={<Marketplace/>} />
        <Route path="marketplace" element={<Marketplace/>} />
        <Route path="myorder" element={<MyOrder/>}/>
        <Route path="orderdetail" element={<Outlet/>} >
            <Route path=":order_id" element={<OrderDetail />} />
        </Route>
        <Route path="account" element={<Account/>}>
        </Route>
        <Route
          path="*"
          element={<Navigate to="/marketplace" />}
        /> 
        
      </Routes>
    );
  };