import styles from "./SingleBrandPage.module.css"
import React,{ useState ,useEffect } from "react";
import { useNavigate } from "react-router-dom";

import { useParams } from "react-router-dom";
import { ReadMoreComp } from "../../components/ReadMoreComp/ReadMoreComp";
import { ErrorFallback } from "../../components/Error/Error";
import { Loading } from "../../components/Loading/Loading";
import { DigitalBrandPlan , DigitalBrandBenefit , BrandAgeConBottomSheet,
     DigitalBrandFaq , TermNConditions ,D2CSubscriptionBoxCard} from "../../components";
import { NavBar } from "../../commonComponents";
import { BrandImgInfoBanner } from "../../commonComponents";
import  Api from "../../utils/Api/Api";

export const SingleBrandPage = ()=>{
    const { vendor_id } = useParams();
    const navigate = useNavigate()

    const [brandData,setBrandData] = useState({
        name:"",
        banner_img:"",
        logo:"",
        desc:"",
        plans:[],
        termNCond:[],
    })
    const [showModal,setShowModal] = useState(false);
    const [modalData,setModalData] = useState({});
    const [loading,setLoading] = useState(true);
    const [hasError,setError] = useState(false);
    const [selectedPlanIdx,setSelectedPlan] = useState(-1)
    const [isD2c,setIsD2c] = useState(false);
    const boxes = [1,2,3,4,5,6]


    const toggleModal = ()=>{
        setShowModal((pre_state)=>(!pre_state))
    }
    const AddToCart = async (details) => {
        try{
            
            if(activeSubscription){
                setShowModal(true);
                return;
            }
            //remove all items from cart
            await Api.EmptyCart();
            // add plan to cart
            await Api.AddToCart(details);
            navigate('/checkout')
        }
        catch(err){
            console.log('=========err===========', err)
        }
    }

    useEffect(()=>{
        const fetchBrandData = async ()=>{
            try {
                let { data: {vendor_data, plans_data } } = await Api.BrandDetails({params:{
                    vendor_id, show_out_of_stock:true
                }});
     
                setBrandData({
                    name:vendor_data.service.service_name,
                    banner_img: vendor_data.detail_page_banner_url,
                    logo:vendor_data.service.logo_url,
                    desc:vendor_data.description,
                    plans:plans_data,
                    termNCond:vendor_data.terms_and_conditions,
                    activeSubscription: vendor_data.is_already_availed_in_subscribe_flow,
                })
                if(vendor_data.show_age_restrictions_popup){
                    setShowModal(true);
                    setModalData({title:'You need to be above 18 years of age',
                     text: 'Disclaimer: This subscription may contain adult language and content. It is not suitable for anyone under the age of 18 and may not be suitable for all adult viewers, view at your discretion.',
                     button2:{
                        text: "NO I'M NOT",
                        action: '/marketplace'
                     },
                     button1: {
                        text:'YES, I AM ABOVE 18',
                        action: vendor_data.vendor_id
                     },
                     type:1
                    })
                }
                else if(vendor_data.is_already_availed_in_subscribe_flow){
                    // setShowModal(true);
                    setModalData({title:`You already have a recurring subscription for ${vendor_data.service.service_name}.`,
                     text: '',
                     button1:{
                        text:'View my subscription',
                        action: '/order'
                     },
                     button2: {
                        text: 'OKAY,Got it',
                        action:''
                     },
                     type:2
            })
                }
            }
            catch(err) {
                setError(true);
            }
            setLoading(false)
        }
        fetchBrandData();
    },[vendor_id])

    if(hasError) {
        return <ErrorFallback />
    }
    if(loading) {
        return <Loading />
    }
    const { benefits, plans, termNCond, overView, faqList, activeSubscription } = brandData;

    return(
            <>
            {showModal && <BrandAgeConBottomSheet toggleModal={toggleModal} modalData={modalData} />}

            <div className={styles.displayDesktop}>
                <NavBar/>
            </div>
            <div className={styles.brandPage}>
                <BrandImgInfoBanner brandData={brandData}/>
                {!isD2c && 
                    <div className={styles.content}>
                        <div className={styles.planSection}>
                            <p className={styles.sectionTitle}>Plan</p>
                            <div className={styles.planList}>
                                {
                                    plans.map((ele,idx)=>(
                                        <DigitalBrandPlan AddToCart={AddToCart} plan={ele} index={idx} selectedPlanIdx={selectedPlanIdx} setSelectedPlan={setSelectedPlan} activeSubscription={activeSubscription} setShowModal={setShowModal} key={idx}/>
                                    ))
                                }
                            </div>
                        </div>
                        {termNCond.length>0 && <TermNConditions terms={termNCond}/>}
                        { 
                            overView && overView.length &&
                            <div className={styles.overViewSection}>
                                <p className={styles.sectionTitle}>Overview</p>
                                <div className={styles.overViewText}>
                                    <ReadMoreComp cut={120} text={overView} />
                                </div>
                            </div>
                        }
                        {
                            selectedPlanIdx!==-1 && <div className={styles.mobSpacer}></div>
                        }
                        {
                            selectedPlanIdx!==-1 && <div className={styles.mobileButtonContainer}>
                                <button
                                onClick={() => AddToCart({
                                    subscription_plan:Number(plans[selectedPlanIdx].subscription_plan_id),
                                    quantity:plans[selectedPlanIdx].quantity
                                    })}
                                 className={styles.payButton}>
                                    BUY NOW
                                </button>
                            </div>
                        } 
                        {benefits && <div className={styles.benefitSection}>
                            <DigitalBrandBenefit Benefits={benefits} />
                        </div>}
                        {faqList && <div className={styles.faqsSection}>
                            <DigitalBrandFaq faqList = {faqList} />
                        </div>}

                    </div>
                }
                {isD2c &&
                    <div className={styles.content}>
                        <div className={styles.boxContainer}>
                            <p className={styles.d2cTitle}>Select a subscription box</p>
                            <div className={styles.boxes}>
                                {boxes.map((ele,idx)=>(
                                    <D2CSubscriptionBoxCard key={idx}/>
                                ))}
                            </div>    
                        </div>  
                        
                    </div>    

                }
        </div>
        </>
    )
}