import styles from "./Checkout.module.css"
import chevronLeft from "../../asset/icons/chevronLeft.png";
import { NavBar } from "../../commonComponents";
import React ,{useState, useEffect} from "react";
import FleekIcon from "../../asset/icons/FleekIcon.png"
import cx from 'classnames';
import  Api from "../../utils/Api/Api";

import { ErrorFallback } from "../../components/Error/Error";
import { Loading } from "../../components/Loading/Loading";
import { useNavigate } from "react-router-dom";


import {PaymentDetailsCard, UserPersonalDetailsCard, ProductPurchaseCard ,
        FreeProductPurchaseCard ,FleekCoinDetailsCard } from "../../components";


export const Checkout = ()=>{
    const navigate = useNavigate()

    const [loading,setLoading] = useState(true);
    const [hasError,setError] = useState(false);
    const [userDetail,setUserDetail] = useState({email:'',phone:''});
    const [cartData,setCartData] = useState([]);
    const [fleekCoins,setFleekCoins] = useState([]);

    
    // Fleek Coin State
    const [useFleekCoin,setUseFleekCoin] = useState(false)

    const toggleUseFleekCoin = ()=>{
        setUseFleekCoin((pre_state)=>(!pre_state))
    }

    useEffect(()=>{
        const fetchCartData = async ()=>{
            try {
                let [{ data }, {data:fleekCoins}, {data: userDetails}] = await Promise.all([Api.GetCartItems(), Api.GetFleekCoins(),
                    Api.GetUserDetails()]);
     
                setCartData(data);
                setFleekCoins(fleekCoins);
                setUserDetail({email:userDetails.email, phone:userDetails.phone_number})
            }
            catch(err) {
                setError(true);
            }
            setLoading(false)
        }
        fetchCartData();
    },[])

    const PlaceOrder = async () => {
        if(!isAbleButton()){
            return;
        }
        const payload = {
            "screen_type": "buy_once",
            "use_coins": useFleekCoin,
            "os":"web",
            "email": userDetail.email,
            "phone_number": userDetail.phone,

        };
        let { data } = await Api.PlaceOrder(payload);

        const payloadPayment = {
            "type":"MKP",
            "is_fleek_money_used":useFleekCoin,
            "currency":"INR",
            "new_placed_ordered_id":data.id, 
            "os" : "web",
            "return_url": process.env.REACT_APP_PAYMENT_URL + '/order'
            }
        const { data: paymentData } = await Api.GetPaymentLink(payloadPayment);
        window.location.href = paymentData.web;

    }
    const handleChange = (e)=>{
        const name = e.target.name;
        const value = e.target.value;
        if(name==="phone" && value.length>10){
            return;
        }
        setUserDetail((pre_state)=>{
            return {...pre_state,[name]:value}
        })
    }
    const eraseInput = (e)=>{
        const name = e.target.name;
        setUserDetail((pre_state)=>{
            return {...pre_state,[name]:''}
        })
    }
    const isAbleButton = ()=>{
        if(userDetail.email?.length>0 && userDetail.phone?.length===10){
            return true
        }
        return false
    }

    if(hasError) {
        return <ErrorFallback />
    }
    if(loading) {
        return <Loading />
    }

    return(
        
        <>
            <div className={styles.headcontent}>
                <NavBar/>
            </div>
            <div className={styles.content}>
                <div className={styles.header}>
                    <p className={styles.headText}>Checkout</p>
                    <img onClick={() => navigate(-1)} className={styles.chevronLeft} src={chevronLeft} alt="chevronLeft"/>
                </div>
                <div className={styles.detailContainer}>
                    <div className={styles.cardContainer}>
                        {
                            cartData.map((d) => {
                                return <ProductPurchaseCard useFleekCoin={useFleekCoin} isDigital={false} cartData= {d} key = {d.id}/>
                            })
                        }
                                    {/* <FreeProductPurchaseCard/> */}

                        
                    </div>
                    <div className={styles.otherDetail}>
                        <FleekCoinDetailsCard fleekCoins={fleekCoins} useFleekCoin={useFleekCoin} toggleUseFleekCoin={toggleUseFleekCoin} cartData= {cartData[0]} />
                        <UserPersonalDetailsCard userDetail={userDetail} handleChange={handleChange} eraseInput={eraseInput} />
                        <PaymentDetailsCard useFleekCoin={useFleekCoin} cartData= {cartData[0]}/>
                        <div className={styles.mobileButtonContainer}>
                            <button onClick={PlaceOrder} className={isAbleButton()? styles.payButton:cx(styles.payButton,styles.inactive)}>
                                PROCEED TO PAY
                            </button>
                        </div>
                        {/* <button className={cx(styles.payButton,styles.desktop)}>
                            PROCEED TO PAY
                        </button> */}
                    </div>
                    
                </div>
                
            </div>
        </>
    )
}