import styles from "./PaymentDetailsCard.module.css"
import FleekCoin from "../../asset/icons/fleek-coin-logo.png"


export const PaymentDetailsCard = ({useFleekCoin, cartData = {}})=>{
    const {mrp_price,cost, effective_cost, actual_discounted_cost, coin_value_reserved} = cartData;
    return(
        <div className={styles.card}>
            <div className={styles.flexRow}>
                <p className={styles.paymentTitle}>Payment details</p>
                <p className={styles.noOfItemTitle}>1 item</p>
            </div>
            <div className={styles.itemContainer}>
                <p className={styles.itemName}>Item total</p>
                <p className={styles.price}> {mrp_price}</p>
            </div>
            {(mrp_price - effective_cost) ? <div className={styles.itemContainer}>
                <p className={styles.itemName}>Total discount</p>
                <p className={styles.price}>-  {mrp_price - effective_cost}</p>
            </div>: '' }
            {useFleekCoin ? <div className={styles.flexRow}>
                <div className={styles.fleekCoinText}>
                    <p>FleekCoin</p>
                    <span><img className={styles.fleekCoin} src={FleekCoin} alt="FleekCoin" /></span>
                </div>
                <p className={styles.fleekPrice}>-  {coin_value_reserved}</p>
            </div> : ''}
            <div className={styles.horizontalBar}>
            </div>
            <div className={styles.flexRow}>
                <p className={styles.totalText}>Total</p>
                <p className={styles.totalPrice}> {useFleekCoin? Math.floor(actual_discounted_cost): Math.floor(cost)}</p>
            </div>
        </div>
    )
}